.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 42px auto 0;
    min-width: 282px;
  }
  
  @media screen and (min-width: 768px) {
    .profile {
      flex-direction: row;
      margin: 40px auto 0;
      max-width: 581px;
    }
  }
  
  @media screen and (min-width: 1024px) {
    .profile {
      max-width: 880px;
    }
  }