.popup__input {
    width: 100%;
    min-height: 27px;
    box-sizing: border-box;
    border-style: none;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    padding: 0;
    margin: 0;
    margin-top: 13px;
    margin-bottom: 5px;
  }
  
  @media screen and (min-width: 768px) {
    .popup__input {
      width: 100%;
    }
  }